import React from 'react';
import {graphql, Link, useStaticQuery} from 'gatsby';
import {AllPostTags, AllTagsPostsWrapper} from './templates.style';
import _ from "lodash";
import {SecTitle} from "../containers/home/posts/style";

const AllTags = () => {
    const Data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(
                sort: { fields: [frontmatter___date], order: DESC }
            ) {
                group(field: frontmatter___tags) {
                    totalCount
                    fieldValue
                }
            }
        }
    `);
    const Tags = Data.allMarkdownRemark.group;
    return (

        <AllTagsPostsWrapper>
            <SecTitle>All Available Tags</SecTitle>
            <AllPostTags className="all_post_tags">
                {Tags.map((tag: any) => (

                    <Link key={tag} to={`/tags/${_.kebabCase(tag.fieldValue)}/`}>
                        {`#${tag.fieldValue} (${tag.totalCount})`}
                    </Link>
                ))}
            </AllPostTags>
        </AllTagsPostsWrapper>
    );
};

export default AllTags;
