import React from 'react';
import Helmet from "react-helmet";
import Sticky from 'react-stickynode';
import {createGlobalStyle, ThemeProvider} from 'styled-components';
import ScrollToTop from 'react-scroll-up';
import Navbar from './navbar/navbar';
import Newsletter from './newsletter/newsletter';
import Footer from './footer/footer';
import ScrollUpButton from './scroll-up-button/scroll-up-button';
import ResetCss from './reset-css';
import {theme} from '../theme';
import AllTags from "../templates/all-tags";
import {ToastProvider} from "react-toast-notifications";

type LayoutProps = {
    children: React.ReactNode;
};

const Layout: React.FunctionComponent<LayoutProps> = ({children}) => {
    const GlobalStyle = createGlobalStyle`
      body {
        background: ${theme.background};
      }
    `
    return (
        <ThemeProvider theme={theme}>
            <Helmet>
                <script>
                    {`
                    (function() { 
                        window.__insp = window.__insp || [];
                        __insp.push(['wid', 857501736]);
                        var ldinsp = function(){
                            if(typeof window.__inspld != "undefined") return; 
                            window.__inspld = 1; 
                            var insp = document.createElement('script'); 
                            insp.type = 'text/javascript'; 
                            insp.async = true; insp.id = "inspsync"; 
                            insp.src = ('https:' == document.location.protocol ? 'https' : 'http') + 
                                '://cdn.inspectlet.com/inspectlet.js?wid=857501736&r=' + 
                                Math.floor(new Date().getTime()/3600000); 
                            var x = document.getElementsByTagName('script')[0]; 
                            x.parentNode.insertBefore(insp, x); 
                        };
                        setTimeout(ldinsp, 0);
                    })();
                    `}
                </script>
            </Helmet>
            <GlobalStyle/>
            <ToastProvider
                placement={"bottom-center"}
                autoDismissTimeout={2500}
                autoDismiss={true}
                transitionDuration={200}
            >
                <ResetCss/>
                <Sticky top={0} innerZ={9999} activeClass="nav-sticky">
                    <Navbar/>
                </Sticky>

                {children}

                <AllTags/>
                <Newsletter/>
                <Footer>
                    {`Copyright  ${new Date().getFullYear()} Rodrigo Favarete`}
                </Footer>
                <ScrollToTop
                    showUnder={300}
                    duration={700}
                    easing="easeInOutCubic"
                    style={{bottom: 30, right: 20}}
                >
                    <ScrollUpButton/>
                </ScrollToTop>
            </ToastProvider>
        </ThemeProvider>
    );
};

export default Layout;
