import React, {useState} from 'react';
import {Link} from 'gatsby';
import {IoIosClose, IoIosSearch} from 'react-icons/io';
import {DrawerProvider} from '../drawer/drawer-context';
import Menu from './menu';
import MobileMenu from './mobile-menu';
import SearchContainer from '../../containers/search/search';
import FontFaceObserver from 'fontfaceobserver';
import Skeleton from 'react-loading-skeleton';

import HeaderWrapper, {
    Logo,
    LogoContent,
    LogoSkeleton,
    MenuWrapper,
    NavbarWrapper,
    NavSearchButton,
    NavSearchFromWrapper,
    NavSearchWrapper,
    SearchCloseButton,
} from './navbar.style';
import LogoImage from '../../images/logo.png';


type NavbarProps = {
    className?: string;
};

const MenuItems = [
    {
        label: 'Home',
        url: '/',
    },
    {
        label: 'About Me',
        url: '/about',
    }
];


const Navbar: React.FunctionComponent<NavbarProps> = (
    {
        className,
        ...props
    }
) => {

    const [state, setState] = useState({
        toggle: false,
        search: ''
    });

    const toggleHandle = () => {
        setState({
            ...state,
            toggle: !state.toggle,
        });
    };

    const [logoFont, setLogoFont] = useState(false);

    // Add all classes to an array
    const addAllClasses = ['header'];

    // className prop checking
    if (className) {
        addAllClasses.push(className);
    }

    // Capitol is the Logo Font
    const fontCapitol = new FontFaceObserver('capitol');

    fontCapitol.load().then(() => {
        setLogoFont(true);
    }).catch(() => {
        console.error('Capitol failed to load!');
    });

    return (
        <HeaderWrapper className={addAllClasses.join(' ')} {...props}>

            <NavbarWrapper className="navbar">
                <DrawerProvider>
                    <MobileMenu items={MenuItems} logo={LogoImage}/>
                </DrawerProvider>
                {
                    logoFont ?
                        <Logo>
                            <Link to="/">
                                <LogoContent>
                                    Favarete
                                </LogoContent>
                            </Link>
                        </Logo>
                        :
                        <LogoSkeleton>
                            <Skeleton/>
                        </LogoSkeleton>
                }
                <MenuWrapper>
                    <Menu items={MenuItems}/>
                </MenuWrapper>
                <NavSearchButton
                    type="button"
                    aria-label="search"
                    onClick={toggleHandle}
                >
                    <IoIosSearch size="23px"/>
                </NavSearchButton>
            </NavbarWrapper>

            <NavSearchWrapper className={state.toggle ? 'expand' : ''}>
                <NavSearchFromWrapper>
                    <SearchContainer/>
                    <SearchCloseButton
                        type="submit"
                        aria-label="close"
                        onClick={toggleHandle}
                    >
                        <IoIosClose/>
                    </SearchCloseButton>
                </NavSearchFromWrapper>
            </NavSearchWrapper>
        </HeaderWrapper>
    );
};

export default Navbar;
